<p><br></p>
<p><span class="rvts58">战争学派（</span><span class="rvts59">Warfare</span><span class="rvts58">）：</span>
<br><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber266.jpg"><span class="rvts28"></span></p>
<p><span class="rvts21">战争学派技能一共有</span><span class="rvts20">15</span><span class="rvts21">个。战争学派技能基于角色武器属性提供额外加成。该派系的技能大多归属于近战职业，具有良好的范围攻击效果和群体控制效果。</span>
<br><span class="rvts21">需要注意的是，玩家也可以使用法杖或魔杖之类的法术武器，之后所有的技能会变为对应的元素攻击类型，此时该技能学派从角色的智慧（</span><span class="rvts20">Intelligence</span><span class="rvts21">）属性中得到额外伤害加成。</span>
<br><span class="rvts21">但是，由于战争学派自身拥有额外每级</span><span class="rvts20">5%</span><span class="rvts21">物理伤害提升，使用法杖和魔杖将不会享有这个提升待遇。还有一点，几乎所有的战争派系技能要装备近战武器，而魔杖并不属于近战武器。</span>
<br><span class="rvts43"></span>
<br><span class="rvts29">战争学派技能书可以在以下</span><span class="rvts33">NPC</span><span class="rvts29">中买到：</span>
<br><span class="rvts20">1. </span><span class="rvts21">卡利亚斯（</span><span class="rvts20">Kalias</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">欢乐堡郊外</span>
<br><span class="rvts20">2. </span><span class="rvts21">商人哈南（</span><span class="rvts20">Trader Haran</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">浮木城</span>
<br><span class="rvts20">3. </span><span class="rvts21">加雷斯（</span><span class="rvts20">Gareth</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">北部废墟，阿玛迪亚避难所</span>
<br><span class="rvts20">4. </span><span class="rvts21">精灵荆棘舞者（</span><span class="rvts20">Elf Thorndancer</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">精灵营地，死神海岸</span>
<br><span class="rvts20">5. </span><span class="rvts21">黑环军需官（</span><span class="rvts20">Black Ring Quartermaster</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">费勒斯神庙南部，无名岛</span>
<br><span class="rvts20">6. </span><span class="rvts21">迷茫骑士（</span><span class="rvts20">The Lost Knight</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">泰森地留斯神庙，无名岛</span>
<br><span class="rvts20">7. </span><span class="rvts21">商人费尔诺拉（</span><span class="rvts20">Trader Fionola</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
